<template>

  <canvas ref="screenCanvas">Canvas not supported {{ refreshCanvas(text) }}</canvas>

</template>

<script>
export default {
  props: ["canvasWidth", "canvasHeight", "text", "textAlign", "backgroundColor", "frameImgUrl", "screenBlobImg"],
  data() {
    return {
    }
  },
  methods: {
    refreshCanvas(data) {
      // 自定义字体 https://www.zhihu.com/question/496036108
      const width = this.canvasWidth; const height = this.canvasHeight;

      const canvas = this.$refs.screenCanvas;
      if (canvas == null) return
      canvas.setAttribute("width", width + "")
      canvas.setAttribute("height", height + "")

      // canvas.style.width = this.canvasWidth / 4.5  + "px";
      // canvas.style.height = this.canvasHeight / 4.5  + "px";

      // 不支持canvas
      if (!canvas.getContext) return

      const ctx2d = canvas.getContext('2d');
      // 绘制背景颜色
      ctx2d.fillStyle = this.backgroundColor
      ctx2d.fillRect(0,0, width, height);

      // 选中的手机边框
      const phoneFrame = {
        path: this.frameImgUrl,
        x: width * 0.05, // 外壳起点
        y: height * 0.18,
        width: 1564,
        height: 3320,
        startX: 59.4, // 内容起点
        startY: 191.2,
        endX: 1501.4, // 内容终点
        endY: 3232.9,
        extraY: 1548.8 // 关机按键
      }

      const phoneFrameToPrevScale = width * 0.9 / phoneFrame.width
      phoneFrame.width = phoneFrame.width *  phoneFrameToPrevScale
      phoneFrame.height = phoneFrame.height *  phoneFrameToPrevScale
      phoneFrame.startX = phoneFrame.x + phoneFrame.startX *  phoneFrameToPrevScale
      phoneFrame.startY = phoneFrame.y + phoneFrame.startY *  phoneFrameToPrevScale
      phoneFrame.endX = phoneFrame.x + phoneFrame.endX *  phoneFrameToPrevScale
      phoneFrame.endY = phoneFrame.y + phoneFrame.endY *  phoneFrameToPrevScale


      // 绘制文字
      if (this.text != null) {
        const text = this.text.split("\n") // 切换真实换行
        ctx2d.beginPath()
        ctx2d.fillStyle = "#FFFFFF"
        if (this.textAlign == 0) {
          ctx2d.textAlign = "left"; // "left" || "right" || "center"
          let upHeight = 1 < text.length? 64 : 100
          for (let i = 0; i < text.length && i < 2; i++) {
            ctx2d.font = "bolder 74px Arial"
            ctx2d.fillText(text[i], phoneFrame.x + 12, 100 * (i + 1) + upHeight, width - phoneFrame.x * 2 - 12);
          }
        } else if (this.textAlign == 2) {
          ctx2d.textAlign = "right"; // "left" || "right" || "center"
          let upHeight = 1 < text.length? 64 : 100
          for (let i = 0; i < text.length && i < 2; i++) {
            ctx2d.font = "bolder 74px Arial"
            ctx2d.fillText(text[i], width - phoneFrame.x - 12, 100 * (i + 1) + upHeight, width - phoneFrame.x * 2 - 12);
          }
        } else {
          ctx2d.textAlign = "center"; // "left" || "right" || "center"
          // 最多绘制两行
          let upHeight = 1 < text.length? 64 : 100
          for (let i = 0; i < text.length && i < 2; i++) {
            ctx2d.font = "bolder 74px Arial"
            ctx2d.fillText(text[i], width / 2, 100 * (i + 1) + upHeight, width - phoneFrame.x * 2);
          }
        }
        ctx2d.closePath()
      }

      // 绘制手机截图

      var screenImg = new Image();
      screenImg.crossOrigin = "Anonymous";
      // 创建 FileReader 对象
      const reader = new FileReader();

      // 设置读取完成时的回调函数
      reader.onload = function (event) {
        // 将读取的数据设置为 Image 对象的 src
        screenImg.src = event.target.result;
      };
      reader.readAsDataURL(this.screenBlobImg);

      screenImg.src = this.screenBlobImg;
      screenImg.onload = function () {
        // 手机截图保证内容区拉伸占满
        const scale = Math.max((phoneFrame.endX - phoneFrame.startX) / this.width, (phoneFrame.endY - phoneFrame.endX) / this.height)
        // console.log("width: " + (phoneFrame.endX - phoneFrame.startX) / this.width + " height: " + this.height + " scale: " + scale);
        ctx2d.drawImage(this, phoneFrame.startX, phoneFrame.startY, this.width * scale, this.height * scale);


        // 绘制手机边框
        let frameImg = new Image();
        frameImg.src = phoneFrame.path;
        console.log("path path", phoneFrame.path)

        frameImg.onload = function () {
          ctx2d.drawImage(this, phoneFrame.x, phoneFrame.y, phoneFrame.width, phoneFrame.height)
        }
      }

    },
    exportImages() {
      const canvas = this.$refs.screenCanvas;
      var dataURL = canvas.toDataURL("image/png"); // 将canvas转换为data URL
      var canvasBlob = this.dataURLtoBlob(dataURL)

      let fileOfBlob = new File([canvasBlob], new Date().getTime() + '.jpg')
      return fileOfBlob
    },
    dataURLtoBlob(dataurl) {
      var arr = dataurl.split(',')
      //注意base64的最后面中括号和引号是不转译的
      var _arr = arr[1].substring(0, arr[1].length - 2)
      var mime = arr[0].match(/:(.*?);/)[1],
          bstr = atob(_arr),
          n = bstr.length,
          u8arr = new Uint8Array(n)
      while (n--) {
        u8arr[n] = bstr.charCodeAt(n)
      }
      return new Blob([u8arr], {
        type: mime
      })
    }
  },
  mounted() {
    this.refreshCanvas()
  },
  computed: {

  },
  watch: {
    canvasWidth(newValue, oldValue) {
      console.log('canvasWidth 变化:', newValue);
      this.refreshCanvas(this.text)
    },
    canvasHeight(newValue, oldValue) {
      console.log('canvasHeight 变化:', newValue);
      // 处理 canvasHeight 变化的逻辑
      this.refreshCanvas(this.text)
    },
    text(newValue, oldValue) {
      console.log('text 变化:', newValue);
      // 处理 text 变化的逻辑
      this.refreshCanvas(this.text)
    },
    backgroundColor(newValue, oldValue) {
      console.log('backgroundColor 变化:', newValue);
      // 处理 backgroundColor 变化的逻辑
      this.refreshCanvas(this.text)
    },
    frameImgUrl(newValue, oldValue) {
      console.log('frameImgUrl 变化:', newValue);
      // 处理 frameImgUrl 变化的逻辑
      this.refreshCanvas(this.text)
    },
    screenBlobImg(newValue, oldValue) {
      console.log('screenBlobImg 变化:', newValue);
      this.refreshCanvas(this.text)
      // 处理 screenBlobImg 变化的逻辑
    },
    textAlign(newValue, oldValue) {
      console.log('screenBlobImg 变化:', newValue);
      this.refreshCanvas(this.text)
      // 处理 screenBlobImg 变化的逻辑
    },
  }
}
</script>
