<template>
  <div class="container" v-loading="isLoading">
    <div class="App">

      <el-steps style="width: 100%; padding-bottom: 48px" :active="activeStep" finish-status="success" >
        <el-step title="Create"></el-step>
        <el-step title="Checkout"></el-step>
        <el-step title="Finish"></el-step>
      </el-steps>

      <div>
        <h3 style="font-size: 20px; padding-top: 20px; padding-bottom: 2px; margin: 0">Checkout Project</h3>

        <div style="margin-top: 48px">

          <form action="https://www.paypal.com/cgi-bin/webscr" method="post" target="_top">
            <input type="hidden" name="cmd" value="_s-xclick" />
            <input type="hidden" name="hosted_button_id" value="GSW22QBV83NK2" />
            <input type="hidden" name="currency_code" value="USD" />
            <input type="image" src="https://www.paypalobjects.com/en_US/i/btn/btn_paynow_LG.gif" border="0" name="submit" title="PayPal - The safer, easier way to pay online!" alt="Buy Now" />
          </form>
        </div>

      </div>

      <div>
        <h3 style="font-size: 20px; padding-top: 20px; padding-bottom: 2px; margin: 0">Create Project</h3>
        <h3 style="font-size: 18px; color: #999999; padding-top: 20px">Basic Info</h3>

        <div style="padding-top: 0" class="Input">
          <h3 style="margin-top: 0">Project Name</h3>
          <input type="text" v-model="projectForm.projectName" placeholder=""/>
        </div>

        <div class="Input">
          <h3>Android Package Name</h3>
          <input type="text" v-model="projectForm.packageName" placeholder=""/>
        </div>

        <h3 style="font-size: 18px; color: #999999; padding-top: 20px">Project Strings</h3>
        <div style="padding-top: 0" class="Input">
          <h3 style="margin-top: 0">string.xml</h3>

          <div class="drop-area" style="min-height: 150px">

          </div>
        </div>


        <h3 style="font-size: 18px; color: #999999; padding-top: 20px">PlayStore Info</h3>
        <div style="padding-top: 0" class="Input">
          <h3 style="margin-top: 0">App name</h3>
          <input type="text" v-model="projectForm.appName" placeholder="Default value is taken from Google play" maxlength="30"/>
        </div>
        <div class="Input">
          <h3>Short description</h3>
          <input type="text" v-model="projectForm.shortDesc" placeholder="Default value is taken from Google play" maxlength="80"/>
        </div>
        <div class="Input">
          <h3 >Full description</h3>
          <textarea dir="auto"   v-model="projectForm.fullDesc" placeholder="Default value is taken from Google play" maxlength="4000"/>
        </div>
        <div class="Input">
          <h3 >Screenshot Data</h3>
          <el-row>
            <el-col :span='12' v-for="item in projectForm.screenPreview">
              <div class="drop-area"
                   @dragover.prevent @drop="handleDrop($event, item)"
                   @dragenter="item.isDragging = true"
                   @dragleave="item.isDragging = false"
                   :class="{ 'highlight-border': item.isDragging }">

                <div style="height: 40px; background-color: white; display: flex; justify-content: space-between; align-items: center">
                  <h4 style="margin: 0"></h4>
                  <el-button type="text" @click="handleOpenEditDrawer(true, item)" style="margin: 16px; font-weight: bold">Edit
                  </el-button>
                </div>

                <screen-preview ref="screenRefs"
                                :class="{ 'highlight-border': item.isDragging }"
                                :key="item"
                                :canvas-width="previewWidth" :canvas-height="previewHeight"
                                :text="item.title"
                                :text-align="item.textAlign"
                                style="width: 100%;"
                                :background-color="item.color"
                                frame-img-url="/frames/google-pixel-4-xl-black.png"
                                :screen-blob-img="item.blob"></screen-preview>
              </div>

            </el-col>
          </el-row>
        </div>

        <el-button :loading="isSubmitLoading" class="Button" @click="handleSubmit">Submit</el-button>

      </div>



    </div>

    <el-drawer
        :title="this.isEditDrawer? 'Edit Screenshot': 'Add Screenshot'"
        :visible.sync="isAddEditDrawerOpen"
        :size="'45%'"
        direction='rtl'>

      <div style="display: flex; flex-direction: column; height: 100%; width: 100%; padding-left: 24px; padding-right: 24px">
        <el-form style="flex: 1; " ref="form" :model="selectedPreview" >

          <h3>Desc</h3>
          <textarea dir="auto" v-model="selectedPreview.title" placeholder=""/>
          <h3 style="margin-top: 26px">Text Align</h3>
          <el-radio v-model="selectedPreview.textAlign" label="0">Left</el-radio>
          <el-radio v-model="selectedPreview.textAlign" label="1">Center</el-radio>
          <el-radio v-model="selectedPreview.textAlign" label="2">Right</el-radio>
          <h3 style="margin-top: 26px">Background</h3>
          <el-color-picker v-model="selectedPreview.color"></el-color-picker>
        </el-form>

      </div>

    </el-drawer>

  </div>
</template>

<script>
import ScreenPreview from "@/components/ScreenPreview.vue";

export default {
  components: {ScreenPreview},
  data() {
    return {
      activeStep: 0,
      isLoading: false,
      isSubmitLoading: false,
      previewWidth: 1080,
      previewHeight: 1920,
      defaultImgBlob: null,
      isAddEditDrawerOpen: false,
      isEditDrawer: false,
      selectedPreview: {},

      projectForm: {
        projectId: '',
        listingId: '',
        projectName: '',
        packageName: "",
        bundleId: '',
        languages: '',
        appName:'',
        shortDesc: '',
        fullDesc: '',
        screenPreview: [
          {
            title: "Input\ntext here",
            textAlign: 0,
            color: "#5ba5be",
            blob: null,
            isDragging: false,
          },
          {
            title: "Drag\nscreenshot here",
            textAlign: 0,
            color: "#ec71a8",
            blob: null,
            isDragging: false
          }
        ]
      },
      languagesArray: ["EN"]
    }
  },
  mounted() {
    this.selectedPreview = this.projectForm.screenPreview[0]
    fetch('/frames/img_previews_demo.jpg')
        .then(response => response.blob())
        .then(blob => {
          this.defaultImgBlob = blob
          for (const item of this.projectForm.screenPreview) {
            if (item.blob == null) {
              item.blob = this.defaultImgBlob
            }
          }
        })
        .catch(error => {
          console.error('blob error:', error);
        });

  },
  methods: {
    makeToast(text, isError) {
      const h = this.$createElement;
      this.$notify({
        duration: 3000,
        showClose: false,
        message: h('i', { style: isError? 'color: black': 'color: teal'}, text)
      });
    },
    handleOpenEditDrawer(isEdit, preview) {
      this.isEditDrawer = isEdit
      this.isAddEditDrawerOpen = true
      if (preview == null) {
        preview = {
          title: "",
          blob: this.defaultImgBlob,
          isDragging: false,
        }
      }
      this.selectedPreview = preview
    },
    handleDrawerAdded() {
      if (!this.isEditDrawer) {
        if (this.selectedPreview.blob == null) {
          this.selectedPreview.blob = this.defaultImgBlob
        }
        this.screenPreview.push(this.selectedPreview)
      }
      this.isAddEditDrawerOpen = false
    },
    handleDrop(event, item) {
      event.preventDefault();
      this.isDragging = false
      const files = event.dataTransfer.files;
      if (0 < files.length) {
        item.blob = files[0]
      }
    },
    handleSubmit() {
      if (this.projectForm.projectName == null || this.projectForm.projectName.length <= 0) {
        this.makeToast("Project name cannot be empty.", true)
        return
      }
      if (this.projectForm.packageName == null || this.projectForm.packageName.length <= 0) {
        this.makeToast("Package name cannot be empty.", true)
        return
      }




    }
  }
}
</script>


